import React, { useContext, useState } from 'react';
import { SidebarContext } from '../context/SidebarContext';
import { SearchIcon, MoonIcon, SunIcon, BellIcon, MenuIcon, OutlinePersonIcon, OutlineCogIcon, OutlineLogoutIcon } from '../icons';
import { Avatar, Badge, Input, Dropdown, DropdownItem, WindmillContext } from '@windmill/react-ui';
import { Link, useHistory } from 'react-router-dom';
import response from '../utils/demo/profileData';
import { useDispatch, useSelector } from 'react-redux';
import { authRemainingSelector } from '../redux/selector';
import { createInstance } from '../services/createInstance';
import AuthSlice from '../redux/AuthSlice';
import { logOutUser } from '../services/authService';

function Header() {
    const { mode, toggleMode } = useContext(WindmillContext);
    const { toggleSidebar } = useContext(SidebarContext);

    const [isNotificationsMenuOpen, setIsNotificationsMenuOpen] = useState(false);
    const [isProfileMenuOpen, setIsProfileMenuOpen] = useState(false);

    function handleNotificationsClick() {
        setIsNotificationsMenuOpen(!isNotificationsMenuOpen);
    }

    function handleProfileClick() {
        setIsProfileMenuOpen(!isProfileMenuOpen);
    }

    const dispatch = useDispatch();
    const user = useSelector(authRemainingSelector);
    const history = useHistory();

    const currentUser = user?.login.currentUser;
    const accessToken = currentUser?.accessToken;

    const handleLogOut = () => {
        let axiosJWT = createInstance(currentUser, dispatch, AuthSlice.actions.logOutSuccess);
        logOutUser(dispatch, history, '1', accessToken, axiosJWT);
    };
    return (
        <header className="w-full z-40 py-5 my-2 bg-white shadow-bottom dark:bg-gray-800">
            <div className="container flex items-center justify-between h-full px-6 mx-auto text-orange-600 dark:text-orange-300">
                {/* <!-- Mobile hamburger --> */}
                <button
                    className="p-1 mr-5 -ml-1 rounded-md lg:hidden focus:outline-none focus:shadow-outline-orange "
                    onClick={toggleSidebar}
                    aria-label="Menu"
                >
                    <MenuIcon className="w-6 h-6" aria-hidden="true" />
                </button>
                {/* <!-- Search input --> */}
                <div className="flex justify-center flex-1 lg:mr-32">
                    <div className="relative w-full max-w-xl mr-6 focus-within:text-orange-500">
                        {/* <div className="absolute inset-y-0 flex items-center pl-2">
                            <SearchIcon className="w-4 h-4" aria-hidden="true" />
                        </div>
                        <Input className="pl-8 text-gray-700" placeholder="Search for projects" aria-label="Search" /> */}
                    </div>
                </div>
                <ul className="flex items-center flex-shrink-0 space-x-6">
                    {/* <!-- Theme toggler --> */}
                    <li className="flex">
                        <button
                            className="rounded-md focus:outline-none focus:shadow-outline-orange"
                            onClick={toggleMode}
                            aria-label="Toggle color mode"
                        >
                            {mode === 'dark' ? (
                                <SunIcon className="w-5 h-5" aria-hidden="true" />
                            ) : (
                                <MoonIcon className="w-5 h-5" aria-hidden="true" />
                            )}
                        </button>
                    </li>
                    {/* <!-- Notifications menu --> */}
                    <li className="relative">
                        <button
                            className="relative align-middle rounded-md focus:outline-none focus:shadow-outline-orange"
                            onClick={handleNotificationsClick}
                            aria-label="Notifications"
                            aria-haspopup="true"
                        >
                            <BellIcon className="w-5 h-5" aria-hidden="true" />
                            {/* <!-- Notification badge --> */}
                            <span
                                aria-hidden="true"
                                className="absolute top-0 right-0 inline-block w-3 h-3 transform translate-x-1 -translate-y-1 bg-red-600 border-2 border-white rounded-full dark:border-gray-800"
                            ></span>
                        </button>

                        <Dropdown align="right" isOpen={isNotificationsMenuOpen} onClose={() => setIsNotificationsMenuOpen(false)}>
                            <DropdownItem tag="a" href="/chats" className="justify-between">
                                <span>Messages</span>
                                <Badge type="danger">13</Badge>
                            </DropdownItem>
                            <DropdownItem tag="a" href="/orders" className="justify-between">
                                <span>Sales</span>
                                <Badge type="danger">2</Badge>
                            </DropdownItem>
                            <DropdownItem onClick={() => alert('Alerts!')}>
                                <span>Alerts</span>
                            </DropdownItem>
                        </Dropdown>
                    </li>
                    {/* <!-- Profile menu --> */}
                    <li className="relative">
                        <button
                            className="rounded-full focus:shadow-outline-orange focus:outline-none"
                            onClick={handleProfileClick}
                            aria-label="Account"
                            aria-haspopup="true"
                        >
                            <Avatar className="align-middle" src={response.avatar} alt="" aria-hidden="true" />
                        </button>
                        <Dropdown align="right" isOpen={isProfileMenuOpen} onClose={() => setIsProfileMenuOpen(false)}>
                            <DropdownItem tag="a" href="/manage-profile">
                                <OutlinePersonIcon className="w-4 h-4 mr-3" aria-hidden="true" />
                                <span>Profile</span>
                            </DropdownItem>
                            <DropdownItem tag="a" href="/settings">
                                <OutlineCogIcon className="w-4 h-4 mr-3" aria-hidden="true" />
                                <span>Settings</span>
                            </DropdownItem>
                            <DropdownItem onClick={handleLogOut}>
                                <OutlineLogoutIcon className="w-4 h-4 mr-3" aria-hidden="true" />
                                <span>Log out</span>
                            </DropdownItem>
                        </Dropdown>
                    </li>
                </ul>
            </div>
        </header>
    );
}

export default Header;
